<template>
      <div class="bg-white shadow-sm p-4">
        <div class="flex justify-center mb-6 mt-4">
          <div class="w-32 h-32 rounded-full overflow-hidden">
            <img src="https://via.placeholder.com/150" alt="Profile Picture" class="w-full h-full object-cover">
          </div>
        </div>
        <form @submit.prevent="validateForm" class="space-y-4">
          <div class="form-group">
            <label for="nickname" class="block text-gray-700">Nickname</label>
            <input v-model="form.nickname" type="text" id="nickname" class="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-3/4 ease-linear transition-all duration-150" placeholder="Enter your nickname">
            <p v-if="errors.nickname" class="text-red-500 text-xs">{{ errors.nickname }}</p>
          </div>
          <div class="form-group mt-5">
            <label for="email" class="block text-gray-700">Email</label>
            <div class="flex items-center mt-1">
              <input v-model="form.email" type="email" id="email" class="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-3/4 ease-linear transition-all duration-150" placeholder="Enter your email">
              <span class="ml-2 text-xs text-green-500">Verified</span>
            </div>
            <p v-if="errors.email" class="text-red-500 text-xs">{{ errors.email }}</p>
          </div>
          <div class="form-group mt-5">
            <label for="phone" class="block text-gray-700">Phone Number</label>
            <div class="flex items-center mt-1">
              <input v-model="form.phone" type="tel" id="phone" class="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-3/4 ease-linear transition-all duration-150" placeholder="Enter your phone number">
              <span class="ml-2 text-xs text-red-500">Not Verified</span>
            </div>
            <p v-if="errors.phone" class="text-red-500 text-xs">{{ errors.phone }}</p>
          </div>
          <div class="form-group mt-5">
            <label for="address" class="block text-gray-700">Address</label>
            <input v-model="form.address" type="text" id="address" class="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-3/4 ease-linear transition-all duration-150" placeholder="Enter your address">
            <p v-if="errors.address" class="text-red-500 text-xs">{{ errors.address }}</p>
          </div>
          <div class="form-group mt-5">
            <label for="detailAddress" class="block text-gray-700">Detail Address</label>
            <input v-model="form.detailAddress" type="text" id="detailAddress" class="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-3/4 ease-linear transition-all duration-150" placeholder="Enter your detail address">
            <p v-if="errors.detailAddress" class="text-red-500 text-xs">{{ errors.detailAddress }}</p>
          </div>
          <div class="form-group mt-5">
            <label for="occupation" class="block text-gray-700">Occupation</label>
            <input v-model="form.occupation" type="text" id="occupation" class="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-3/4 ease-linear transition-all duration-150" placeholder="Enter your occupation">
            <p v-if="errors.occupation" class="text-red-500 text-xs">{{ errors.occupation }}</p>
          </div>
          <div class="form-group mt-5">
            <label for="bio" class="block text-gray-700">Bio</label>
            <textarea v-model="form.bio" id="bio" class="border-0 px-3 py-3 placeholder-blueGray-300 text-blueGray-600 bg-white rounded text-sm shadow focus:outline-none focus:ring w-full ease-linear transition-all duration-150" rows="3" placeholder="Write a short bio"></textarea>
            <p v-if="errors.bio" class="text-red-500 text-xs">{{ errors.bio }}</p>
          </div>
          <div class="flex justify-center mt-6">
            <button type="submit" class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded uppercase">Save</button>
          </div>
        </form>
      </div>
  </template>
  
  <script setup lang="ts">
  import { ref, reactive } from 'vue';
  
  interface Form {
    nickname: string;
    email: string;
    phone: string;
    address: string;
    detailAddress: string;
    occupation: string;
    bio: string;
  }
  
  const form = reactive<Form>({
    nickname: '',
    email: '',
    phone: '',
    address: '',
    detailAddress: '',
    occupation: '',
    bio: '',
  });
  
  const errors = reactive<Partial<Form>>({});
  
  const validateForm = () => {
    clearErrors();
    
    if (!form.nickname) errors.nickname = 'Nickname is required.';
    if (!form.email) errors.email = 'Email is required.';
    if (!form.phone) errors.phone = 'Phone number is required.';
    if (!form.address) errors.address = 'Address is required.';
    if (!form.detailAddress) errors.detailAddress = 'Detail address is required.';
    if (!form.occupation) errors.occupation = 'Occupation is required.';
    if (!form.bio) errors.bio = 'Bio is required.';
  
    if (Object.values(errors).every(error => !error)) {
      // Save the form data
      console.log('Form data:', form);
    }
  };
  
  const clearErrors = () => {
    Object.keys(errors).forEach(key => {
      errors[key as keyof Form] = '';
    });
  };
  </script>
  
  <style scoped>
  .form-input {
    border: 1px solid #d1d5db;
    padding: 0.5rem;
    border-radius: 0.375rem;
  }
  .form-input:focus {
    border-color: #3b82f6;
    outline: none;
  }
  .form-textarea {
    border: 1px solid #d1d5db;
    padding: 0.5rem;
    border-radius: 0.375rem;
  }
  .form-textarea:focus {
    border-color: #3b82f6;
    outline: none;
  }
  </style>
  